.users{
    background-color: #FFFFFF;
    border-radius: 4px;
    padding: 0 0 15px 0;
    margin-bottom: 15px;
}
.searchPanel{
    display: grid;
    grid-template: 46px / auto 1fr auto;
    padding: 0 15px;
    grid-gap: 15px;
    border-bottom: 1px solid #DCE1E6;


}
.searchPanel__icon{
    width: 25px;
    height: 25px;
    align-self: center;
    justify-self: flex-end;
}
.searchPanel__input{
    height: 30px;
    align-self: center;

}
.searchPanel__button{
    margin: auto;
    justify-self: center;
    font-size: 13px;
    background-color: #5181B8;
    padding: 7px 16px 8px;
    color: #FFFFFF;
    border-radius: 4px;
}
.localHeader{
    padding: 0 10px;
    border-bottom: 1px solid #DCE1E6;
    display: flex;
    height: 45px;
    align-items: center;


}
.mainTitle{
    background-color: #FFFFFF;
    font-size: 13px;
    color: black;
    margin-right: 10px;
}
.counter{
    font-size: 13px;
    padding: 5px;
    background-color: #DCE1E6;
}
.preloaderContainer{
    padding: 0  15px;
    display: flex;
    justify-content: center;
}


.following{
    padding: 0 20px;
}

.userSection{
    border-bottom: 1px solid #DCE1E6;
    padding: 12px 0;
    display: grid;
    grid-template: auto / auto 1fr auto ;
    grid-gap: 10px;
}
.ava{
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;



}
.userInfoCont{
    display: grid;
    grid-template: auto auto auto / 1fr;
    font-size: 13px;


}
.userName{
    font-weight: 700;
    color: #5181B8;
}
.status{}
.buttonCont{}
.actionButtonUnfollow{
    padding: 7px 0;
    text-align: center;
    width: 100px;
    border-radius: 4px;
    background-color: #EDEEF0;
}
.actionButtonFollow{
    padding: 7px 0;
    text-align: center;
    width: 100px;
    border-radius: 4px;
    background-color: #5181B8;
    color: white;
}

.pagesControl{
    display: flex;
    justify-content: center;
    margin-top: 15px;

}
.pageButton{
    text-align: center;
    padding: 10px 10px;
    font-size: 14px;
    background-color: #DCE1E6;
    border-radius: 4px;
    margin: 0 10px;
}
.pageButton:last-child{
    margin-left: 20px;
}
.pageButton:first-child{
    margin-right: 20px;
}
.activePageCont{
    display: flex;
    margin: 0 0px;
}

.arrow{
    height: 34px;
}
